.footer{
  margin-top: 10px;
  background-color: #1392cd;
  font-family: 'Fira Sans', sans-serif;
  color: white;
  &__li{
    display: flex;
    align-items: center;
    column-gap: 10px;


  }
  &__logo{
    font-size: 18px;
  }
  &__smagic{
    width: 150px;
    height: 55px;
  }
  &__sky{
    width: 170px;
    height: 35px;
  }

  & .container{
    padding: 48px 0 66px;
  }
  &__lists{
    display: flex;
    justify-content: space-between;
    align-items: start;
    column-gap: 50px;
  }
  &__list li:first-child {
    font-size: 22px;
    color: #fff;
    margin-bottom: 32px;
  }
  &__list{
    padding: 40px;
  }

  &__list li{
    a{
      color: rgba(255, 255, 255, 0.9);
    }
    font-size: 14px;
    font-weight: normal;
    line-height: normal;
    margin-bottom: 20px;
    list-style-type: none;
  }

  &__social {
  ul{
    display: flex;
    align-items: center;
    column-gap: 30px;
  }

  li a{
    color: white;
    font-size: 30px;
  }
  }
  &__contact a{
    color: white;
    font-size: 24px;
  }
  &__email a {
    color: white;
    font-size: 24px;
   column-gap: 10px;
    display: flex;
    align-items: center;
  }
  .footer__connection{
    display: flex;
    align-items: center;
    justify-content: space-around;
    border-top: 1px solid #FFFFFF;
    padding: 30px 0;
    margin-top: 15px;
  }
}